*{
    box-sizing: border-box;
    padding: 0;
    margin:0;
}
.homeContainer{
    .bgCloud{
        position:absolute;
        z-index:-9;
        width:48%;
        right: 0;
    }
//navbar
    .navAndHomeContainer{
        width: 85%;
        margin:0 auto;
        .navBarContainer{
            width:75%;
            padding-top: 20px;
            .imgLogo{
                width:100%;
                margin-right: 10px;
            }
            .logoTitle{
                font-family: 'Roboto';
                font-style: normal;
                font-weight: 600;
                font-size: 24px;
                line-height: 28px;
                color: #000000;
                text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
            }
            .links{
                font-family: 'Roboto';
                font-style: normal;
                font-weight: 700;
                font-size: 18px;
                line-height: 21px;
                color: #000000;
                padding:10px;
                cursor:pointer;
                &:hover{
                    background: linear-gradient(180deg, #C5DCFF 0%, rgba(197, 220, 255, 0.5) 96.87%, rgba(196, 196, 196, 0) 100%);
                    border-radius: 5px;
                    color:red;
                }
                &:active{
                    background: linear-gradient(180deg, #C5DCFF 0%, rgba(197, 220, 255, 0.5) 96.87%, rgba(196, 196, 196, 0) 100%);
                    border-radius: 5px;
                    color: #000000;
                }
            }
        }

//homebody
       .homeDesc{
         .homeTitle{
            width: 90%;
            font-family: 'Roboto';
            font-style: normal;
            font-weight: 800;
            font-size: 50px;
            line-height: 74px;
            display: flex;
            align-items: center;
            color: #000000;
         }  
         .homeDesc{
            font-family: 'Roboto';
            font-style: normal;
            font-weight: 400;
            font-size: 20px;
            line-height: 23px;
            text-align: justify;
         }
         .imgPersons{
            width: 100%;
            object-fit: cover;
        }
       }
       
//blockchain
       .blockchainContainer{

            .imgBlockchain{
                width:100%;
                object-fit: cover;
            }
            .blockchainInfoContainer{
                .title{
                    font-family: 'Roboto';
                    font-style: normal;
                    font-weight: 400;
                    font-size: 48px;
                    line-height: 56px;
                }
                .titleDesc{
                    font-family: 'Roboto';
                    font-style: normal;
                    font-weight: 400;
                    font-size: 34px;
                    line-height: 28px;
                }
                .body{
                    font-family: 'Roboto';
                    font-style: normal;
                    font-weight: 400;
                    font-size: 20px;
                    line-height: 23px;
                    text-align: justify;
                    color: #000000;
                }
                .button{
                    background: linear-gradient(68.08deg, #9083D5 9.64%, #807593 92.49%);
                    border-radius: 15px;
                    font-family: 'Roboto';
                    font-style: normal;
                    font-weight: 700;
                    font-size: 24px;
                    line-height: 28px;
                }
            }            

       }

//cloud
       .cloudContainer{
            margin-bottom: 240px;
            .imgCloudchain{
                width:100%;
                object-fit: cover;
            }
            .cloudInfoContainer{
                .title{
                    font-family: 'Roboto';
                    font-style: normal;
                    font-weight: 400;
                    font-size: 48px;
                    line-height: 56px;
                }
                .titleDesc{
                    font-family: 'Roboto';
                    font-style: normal;
                    font-weight: 400;
                    font-size: 34px;
                    line-height: 28px;
                }
                .body{
                    font-family: 'Roboto';
                    font-style: normal;
                    font-weight: 400;
                    font-size: 20px;
                    line-height: 23px;
                    text-align: justify;
                    color: #000000;
                }
                .button{
                   background: linear-gradient(68.08deg, #9083D5 9.64%, #807593 92.49%);
                    border-radius: 15px;
                    font-family: 'Roboto';
                    font-style: normal;
                    font-weight: 700;
                    font-size: 24px;
                    line-height: 28px; 
                }
            }            

       }


    }

}


